$text-color: $dark-gray;

.about-section {
    padding: $section-padding 0;
    background: $white;

    .section-header {
        text-align: center;
        margin-bottom: 60px;
        padding: 0 20px;

        .section-title {
            font-family: $font-family-heading;
            font-size: clamp(1.8rem, 4vw, 2.5rem);
            color: $primary;
            margin-bottom: 20px;
        }

        .section-divider {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 15px;
            margin: 20px 0;

            .divider-line {
                width: 60px;
                height: 2px;
                background: $secondary;
            }

            i {
                color: $secondary;
                font-size: 1.5rem;
            }
        }

        .section-subtitle {
            color: $text-color;
            font-size: clamp(1rem, 2vw, 1.1rem);
            margin-top: 15px;
        }
    }

    .about-content {
        max-width: 1200px;
        margin: 0 auto;
        padding: 0 20px;

        .about-subtitle {
            font-family: $font-family-heading;
            font-size: clamp(1.5rem, 3vw, 2rem);
            color: $primary;
            text-align: center;
            margin-bottom: 30px;
        }

        .about-text {
            text-align: center;
            max-width: 800px;
            margin: 0 auto 40px;
            font-size: clamp(1rem, 2vw, 1.1rem);
            line-height: 1.8;
            color: $text-color;
        }

        .mission-box {
            background: $light-gray;
            padding: clamp(20px, 4vw, 40px);
            border-radius: $border-radius;
            text-align: center;
            max-width: 800px;
            margin: 40px auto;
            box-shadow: 0 5px 15px rgba($black, 0.05);

            h4 {
                font-family: $font-family-heading;
                font-size: clamp(1.4rem, 3vw, 1.8rem);
                color: $primary;
                margin-bottom: 20px;
            }

            p {
                font-size: clamp(1rem, 2vw, 1.1rem);
                line-height: 1.8;
                color: $text-color;
            }
        }

        .about-image {
            width: 100%;
            max-width: 1000px;
            margin: 40px auto;
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;

            img {
                width: 100%;
                height: auto;
                object-fit: cover;
                object-position: center;
                display: block;
            }
        }
    }

    .values-box {
        max-width: 1200px;
        margin: 60px auto;
        padding: 0 20px;

        h4 {
            font-family: $font-family-heading;
            font-size: clamp(1.5rem, 3vw, 2rem);
            color: $primary;
            text-align: center;
            margin-bottom: 40px;
            position: relative;

            &:after {
                content: '';
                position: absolute;
                bottom: -15px;
                left: 50%;
                transform: translateX(-50%);
                width: 60px;
                height: 3px;
                background: $secondary;
                border-radius: 2px;
            }
        }

        .values-grid {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
            gap: clamp(15px, 2vw, 30px);
            max-width: 1200px;
            margin: 0 auto;

            .value-item {
                background: $white;
                padding: clamp(20px, 3vw, 30px) clamp(15px, 2vw, 20px);
                border-radius: $border-radius;
                text-align: center;
                box-shadow: 0 5px 15px rgba($black, 0.05);
                border: 1px solid rgba($secondary, 0.1);
                transition: $transition-base;

                &:hover {
                    transform: translateY(-5px);
                    box-shadow: 0 15px 30px rgba($black, 0.1);

                    .value-icon {
                        background: $secondary;

                        i {
                            color: $white;
                        }
                    }
                }

                .value-icon {
                    width: clamp(50px, 8vw, 70px);
                    height: clamp(50px, 8vw, 70px);
                    background: rgba($secondary, 0.1);
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin: 0 auto 20px;
                    transition: $transition-base;

                    i {
                        font-size: clamp(1.4rem, 2.5vw, 1.8rem);
                        color: $secondary;
                        transition: $transition-base;
                    }
                }

                span {
                    color: $primary;
                    font-weight: 600;
                    font-size: clamp(1rem, 1.5vw, 1.1rem);
                }
            }
        }
    }

    .team-section {
        max-width: 1200px;
        margin: 80px auto 0;
        padding: 0 20px;

        .team-title {
            font-family: $font-family-heading;
            font-size: clamp(1.8rem, 4vw, 2.5rem);
            color: $primary;
            text-align: center;
            margin-bottom: 50px;
            position: relative;

            &:after {
                content: '';
                position: absolute;
                bottom: -15px;
                left: 50%;
                transform: translateX(-50%);
                width: 80px;
                height: 3px;
                background: $secondary;
                border-radius: 2px;
            }
        }

        .team-grid {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
            gap: clamp(15px, 2vw, 30px);
            max-width: 1200px;
            margin: 0 auto;

            .team-member {
                background: $white;
                border-radius: $border-radius;
                overflow: hidden;
                box-shadow: 0 5px 15px rgba($black, 0.05);
                transition: $transition-base;

                &:hover {
                    transform: translateY(-5px);
                    box-shadow: 0 15px 30px rgba($black, 0.1);

                    .member-image img {
                        transform: scale(1.05);
                    }
                }

                .member-image {
                    width: 100%;
                    height: 280px;
                    overflow: hidden;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        object-position: center;
                        transition: transform 0.5s ease;
                    }
                }

                .member-info {
                    padding: clamp(15px, 3vw, 20px);
                    text-align: center;

                    h4 {
                        font-family: $font-family-heading;
                        font-size: clamp(1.1rem, 2vw, 1.3rem);
                        color: $primary;
                        margin-bottom: 10px;
                    }

                    p {
                        color: $secondary;
                        font-size: clamp(0.9rem, 1.5vw, 1rem);
                        margin: 0;
                        font-weight: 500;
                    }
                }
            }
        }
    }
}

@media (max-width: $breakpoint-xl) {
    .about-section {
        .team-section .team-grid {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

@media (max-width: $breakpoint-lg) {
    .about-section {
        .team-section .team-grid {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

@media (max-width: $breakpoint-md) {
    .about-section {
        padding: clamp(40px, 6vw, 60px) 0;

        .about-content {
            .mission-box {
                margin: 30px auto;
            }

            .about-image {
                margin: 30px auto;
            }
        }

        .values-box {
            margin: 40px auto;
        }

        .team-section {
            margin-top: 60px;
            
            .team-grid {
                grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
                max-width: 600px;
            }
        }
    }
}

@media (max-width: $breakpoint-sm) {
    .about-section {
        .team-section .team-grid {
            grid-template-columns: 1fr;
            max-width: 350px;
        }
    }
}
